<template><section><h1>CesiumTerrainProvider</h1>
<p><code v-pre>vc-provider-terrain-cesium</code> 组件用于加载 Cesium 格式的地形，如果<code v-pre>url</code>为空，默认通过 <code v-pre>Cesium.createWorldTerrain</code> 加载 CesiumIon 在线全球地形。</p>
<h2>示例</h2>
<h3>加载 Cesium 格式地形</h3>
<h4>预览</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-layer-imagery>
          <vc-provider-imagery-bingmaps url="https://dev.virtualearth.net" bmKey="AgcbDCAOb9zMfquaT4Z-MdHX4AsHUNvs7xgdHefEA5myMHxZk87NTNgdLbG90IE-" mapStyle="Aerial"></vc-provider-imagery-bingmaps>
        </vc-layer-imagery>
        <vc-provider-terrain-cesium ref="terrain" :requestWaterMask="requestWaterMask"></vc-provider-terrain-cesium>
        <vc-navigation></vc-navigation>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
<h4>代码</h4>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"viewer"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">vc-viewer</span> <span class="hljs-attr">@ready</span>=<span class="hljs-string">"ready"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">vc-layer-imagery</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">vc-provider-imagery-bingmaps</span>
          <span class="hljs-attr">url</span>=<span class="hljs-string">"https://dev.virtualearth.net"</span>
          <span class="hljs-attr">bmKey</span>=<span class="hljs-string">"AgcbDCAOb9zMfquaT4Z-MdHX4AsHUNvs7xgdHefEA5myMHxZk87NTNgdLbG90IE-"</span>
          <span class="hljs-attr">mapStyle</span>=<span class="hljs-string">"Aerial"</span>
        &gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vc-provider-imagery-bingmaps</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">vc-layer-imagery</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">vc-provider-terrain-cesium</span> <span class="hljs-attr">ref</span>=<span class="hljs-string">"terrain"</span> <span class="hljs-attr">:requestWaterMask</span>=<span class="hljs-string">"requestWaterMask"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vc-provider-terrain-cesium</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">vc-viewer</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
      <span class="hljs-keyword">return</span> {}
    },
    <span class="hljs-attr">methods</span>: {
      ready(cesiumInstance) {
        <span class="hljs-keyword">const</span> { Cesium, viewer } = cesiumInstance
        <span class="hljs-keyword">var</span> target = <span class="hljs-keyword">new</span> Cesium.Cartesian3(<span class="hljs-number">300770.50872389384</span>, <span class="hljs-number">5634912.131394585</span>, <span class="hljs-number">2978152.2865545116</span>)
        <span class="hljs-keyword">var</span> offset = <span class="hljs-keyword">new</span> Cesium.Cartesian3(<span class="hljs-number">6344.974098678562</span>, <span class="hljs-number">-793.3419798081741</span>, <span class="hljs-number">2499.9508860763162</span>)
        viewer.camera.lookAt(target, offset)
        viewer.camera.lookAtTransform(Cesium.Matrix4.IDENTITY)
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
<h2>属性</h2>
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>url</td>
<td>String</td>
<td></td>
<td><code v-pre>required</code> 指定服务地址。</td>
</tr>
<tr>
<td>requestVertexNormals</td>
<td>Boolean</td>
<td><code v-pre>false</code></td>
<td><code v-pre>optional</code> 指定是否请求其他光照信息，否则使用每个顶点法线。</td>
</tr>
<tr>
<td>requestWaterMask</td>
<td>Boolean</td>
<td><code v-pre>false</code></td>
<td><code v-pre>optional</code> 指定是否请求水面数据。</td>
</tr>
<tr>
<td>requestMetadata</td>
<td>Boolean</td>
<td><code v-pre>true</code></td>
<td><code v-pre>optional</code> 指定是否请求每个切片元数据。</td>
</tr>
<tr>
<td>ellipsoid</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> 指定参考椭球体。</td>
</tr>
<tr>
<td>credit</td>
<td>String</td>
<td></td>
<td><code v-pre>optional</code> 指定服务的描述信息</td>
</tr>
</tbody>
</table>
<hr>
<p>参考官方文档： <a href="https://cesium.com/docs/cesiumjs-ref-doc/CesiumTerrainProvider.html">CesiumTerrainProvider</a></p>
<h2>事件</h2>
<table>
<thead>
<tr>
<th>事件名</th>
<th>参数</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject}</td>
<td>该组件渲染完毕时触发，返回 Cesium 类, viewer 实例，以及当前组件的 cesiumObject。</td>
</tr>
</tbody>
</table>
<hr>
</section></template>

<script>
    export default {
      data () {
        return {
          requestWaterMask: true
        }
      },
      methods: {
        ready (cesiumInstance) {
          const {Cesium, viewer} = cesiumInstance
          var target = new Cesium.Cartesian3(300770.50872389384, 5634912.131394585, 2978152.2865545116)
          var offset = new Cesium.Cartesian3(6344.974098678562, -793.3419798081741, 2499.9508860763162)
          viewer.camera.lookAt(target, offset)
          viewer.camera.lookAtTransform(Cesium.Matrix4.IDENTITY)
        }
      }
    }
  </script>